<template>
    <div class="container mx-auto py-5 flex flex-wrap">
        <div class="lg:flex sm:block">
            <img :src="posterPath" alt="" class="w-64 mx-auto mb-5 block">
            <div class="ml-20">
                <h1 class="text-4xl font-semibold">{{  this.movie.title }}</h1>
                <span class="text-gray-500 text-sm">{{ roundedVoteAverage }} % | {{ this.movie.release_date }}
                    <span :key="index" v-for="(item, index) in movie.genres">
                        {{ item.name }}<span class="mr-1" v-if="movie.genres.length - 1 != index">, </span>
                    </span>
                </span>
                <p class="mt-5">
                    {{this.movie.overview}}
                </p>
                
                <div class="mt-5">
                    <span class="mt-5 font-semibold">Featured Cast</span>
                    <div :key="index" v-for="(crew, index) in movie.credits.crew">
                        <div v-if="index < 2" class="flex flex-col mt-5">
                            <span>{{ crew.name }}</span>
                            <span class="text-gray-500">{{ crew.job }}</span>
                        </div>
                    </div>
                </div>
                <div class="mt-10 flex">
                    <a @click.prevent="openYoutubeModel" :href="youtubeVideo()" target="_blank" class="rounded bg-yellow-500 px-5 py-4 text-black mr-2">Play trailer</a>
                </div>
                
            </div>
        </div>
        <Cast class="mt-20" :casts = "movie.credits.cast" />
        <MediaModel v-model="modelOpen" :mediaURL="mediaURL" :isVideo="this.isVideo"/>
    </div>
</template>
<script>
import Cast from "@/components/movies/Cast";
import MediaModel from "@/components/models/MediaModel";
export default {
    components: {
        Cast,
        MediaModel,
    },
    data() {
        return {
            movie: {
                credits: {
                    crew: {}
                },
                images: {
                    backdrops: {}
                }
            },
            modelOpen: false,
            isVideo: false,
            mediaURL: "",
        }
    },
    mounted() {
        this.fetchMovie(this.$route.params.id);
    },
    computed: {
        posterPath() {
            return "https://image.tmdb.org/t/p/w500/" + this.movie.poster_path
        },
        roundedVoteAverage() {
            return Math.round(this.movie.vote_average * 10);
        }
    },
    watch: {
        "$route.params.id": {
            handler() {
                this.fetchMovie(this.$route.params.id);
            },
            immediate: true,
        }
    },
    methods: {
        async fetchMovie(movieId) {
            const response = await this.$tmdbApi.get(
            '/movie/'+ movieId + '?append_to_response=credits,videos,images'
            );
            this.movie = response.data;
        },
        youtubeVideo() {
            if(!this.movie.videos) return;
            return "https://www.youtube.com/embed/" + this.movie.videos.results[0].key;
        },
        openYoutubeModel() {
            this.mediaURL = this.youtubeVideo();
            this.isVideo = true;
            this.modelOpen = true;
        },
        openImageModel() {
            this.isVideo = false;
            this.modelOpen = true;
        },
        showImageModel(imageFullPath) {
            this.mediaURL = imageFullPath;
            this.isVideo = false;
            this.modelOpen = true;
        }
    } 
}
</script>
