<template>
    <div class="flex items-center w-full sm:w-auto">
        <ul class="hidden sm:flex items-center justify-center h-10">
            <li class="text-center h-full">
                <router-link to="/" class="flex items-center h-full px-6">
                    Movies
                </router-link>
            </li>
            <li class="text-center h-full">
                <router-link to="/actors" class="flex items-center h-full px-6">
                    Actors
                </router-link>
            </li>
        </ul>
        <Slide :closeOnNavigation="true" right class="sm:hidden">
            <ul class="flex flex-col h-10 pl-10">
                <li class="h-full mb-8">
                    <router-link to="/" class="flex items-center h-full text-white">
                        Movies
                    </router-link>
                </li>
                <li class="h-full mb-8">
                    <router-link to="/actors" class="flex items-center h-full text-white">
                        Actors
                    </router-link>
                </li>
            </ul>
        </Slide>
        <div class="bg-[#312f4ae3] rounded-[20px] ml-5 sm:ml-0">
            <input type="text" class="rounded-full bg-transparent px-5 w-[150px] sm:w-auto h-10 focus:outline-none focus:outline-shadow" 
            placeholder="Search..."
            ref="searchBox"
            @focus="handleFocus"
            @input="debounceSearch"
            v-model="searchTerm" />
            <i class="ri-search-line mr-3 text-[20px]"></i>
            <div class="absolute mt-2 rounded w-60 bg-[#313151] z-10">
                <ul class="mt-3" v-if="showSearchResult">
                    <li :key="index" v-for="(movie, index) in searchResult">
                        <router-link :to="`/movie/${movie.id}`" @click.native="showSearchResult = false"
                        class="flex items-center border-b border-gray-500 p-1">
                            <img :src="posterPath(movie.poster_path)" alt="" class="w-10">
                            <span class="ml-2">{{  movie.title }}</span>
                        </router-link>
                    </li>
                </ul>
                <ul class="px-3" v-if="searchResult.length == 0 && showSearchResult">
                    <li>No result found for {{ searchTerm }}</li>
                </ul>
            </div>

            <!-- <AccountDropDown />-->
        </div>
    </div>
</template>
<script>
import { Slide } from 'vue-burger-menu'
export default {
    components: {
        Slide
    },
    data() {
        return {
            searchResult: [],
            searchTerm: "",
            showSearchResult: false
        }
    },
    mounted() {
        this.keyboardEvents();
    },
    methods: {
        debounceSearch(event) {
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                if(event.target.value.length >2) {
                    this.fetchSearch(event.target.value);
                } else {
                    this.showSearchResult = false;
                }
            }, 600);
        },
        async fetchSearch(term) {
            try {
                const response = await this.$tmdbApi.get(
                "/search/movie?query=" + term
                );
                this.searchResult = response.data.results;
                this.searchResult = this.searchResult.filter(movie => movie.backdrop_path !== null);
                this.showSearchResult = response.data.results ? true : false;
            } catch (error) {
                console.log(error);
            }
        },
        handleFocus() {
            if(this.searchTerm != '') {
                this.showSearchResult = true;
            }
        },
        keyboardEvents() {
            let windowObj = this;
            window.addEventListener("click", (e) => {
                if(!this.$el.contains(e.target)) {
                    this.showSearchResult = false;
                }
                
            });
            window.addEventListener("keypress", (e)=> {
                if(e.keyCode == '47') {
                    e.preventDefault();
                    windowObj.$refs.searchBox.focus();
                }
            })
            window.addEventListener('keydown', (e) => {
                if(e.key == 'Escape') {
                    this.showSearchResult = false;
                }
            });
        },
        posterPath(poster_path) {
            if(poster_path) {
                return "https://image.tmdb.org/t/p/w500/" + poster_path
            } else {
                return 'https://via.placeholder.com/50x75';
            }
            
        }
    }
}
</script>
