<template>
    <div class="container mx-auto px-10 py-16">
        <h2 class="mb-10">Popular actors</h2>
        <div class="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-x-8">
            <ActorItem :key="actor.id" v-for="actor in this.actors" :actor="actor"/>
        </div>
        <div class="text-center mt-5">
            <a href="" v-on:click.prevent="previous()">Previous</a>
            <a href="" v-on:click.prevent="next()" class="ml-5">Next</a>
        </div>
        <!-- <div>
            <div class="spinner flex justify-center"> </div>
        </div> -->
    </div>
</template>
<script>
let currentPage = 1;
import ActorItem from "../items/ActorItem.vue"
export default {
    data() {
        return {
            actors: [],
        }
    },
    components: {
        ActorItem,
    },

    mounted() {
        this.fetchActors(currentPage);
        //this.scroll();
    },
    methods: {
        async fetchActors(page) {
            try {
                const response = await this.$tmdbApi.get(
                "https://api.themoviedb.org/3/person/popular?page=" + page
                );
                //this.actors.push(...response.data.results);
                this.actors = response.data.results;
                this.actors = this.actors.filter(actor => actor.profile_path !== null);

            } catch (error) {
                console.log(error);
            }
        },
        scroll() {
            window.onscroll = () => {
                let bottomOfWindow = document.documentElement.scrollTop + window.innerHeight ===
                document.documentElement.offsetHeight;

                if(bottomOfWindow) {
                    currentPage += 1;
                    this.fetchActors((currentPage += 1));
                }
            }
        },
        previous() {
            currentPage -= 1;
            this.fetchActors((currentPage));
        },
        next() {
            currentPage += 1;
            this.fetchActors((currentPage));
        }
    },
}
</script>
