<template>
    <div class="w-full px-10 lg:px-0">
        <h2>Cast</h2>
        <div class="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 mt-10">
            <div class="mr-2 mb-10" :key="index" v-for="(cast, index) in casts">
                <router-link :to="`/actor/${cast.id}`">
                    <img :src="castProfileImg(cast)" alt="" class="rounded-[20px] h-[350px] w-[250px] object-cover object-top hover:opacity-75 transition-all">
                    <h3 class="mt-2 text-[18px]">{{ cast.name }}</h3>
                </router-link>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'Cast',
        props: {
            casts: {
                required: true,
            },
        },
        methods: {
            castProfileImg(cast) {
                if(cast.profile_path) {
                    return "https://image.tmdb.org/t/p/w300/" + cast.profile_path;
                } else {
                    return 'https://via.placeholder.com/300x450';
                }
            }
        },
    }
</script>
