<template>
    <div class="mb-10">
        <router-link :to="`/actor/${this.actor.id}`">
            <img :src="profileImage" alt="" class="rounded-[20px] h-[330px] w-[250px] object-cover object-top hover:opacity-75 transition-all">
            <h3 class="mt-2 text-[20px]">{{ actor.name }}</h3>
            <p class="text-gray-400">
                {{ knownFor }}
            </p>
        </router-link>
    </div>
</template>
<script>
export default {
    props: {
        actor: {
            required: true,
        },
    },

    computed: {
        profileImage() {
            return "https://image.tmdb.org/t/p/w235_and_h235_face/" + this.actor.profile_path;
        },
        knownFor() {
            let knownStr = "";
            for(let i; i < this.actor.known_for.length -1; i++) {
                knownStr+= this.actor.known_for[i].title;
            }

            return knownStr;
        }
    }
}
</script>
