            <div class="showcase-img absolute top-0 left-0 right-0 w-full z-[-1]">
                <img v-if="selectedMovie" class="h-[58vw] sm:h-[38vw] w-full object-cover" :src="showcaseImgSrc" alt="">
            </div>
            <div class="showcase-details mt-[18vw]">
                    <span v-if="selectedMovie">{{ formatDate }}</span>
                    <h2 class="text-[30px]" v-if="selectedMovie">{{ selectedMovie.title }}</h2>
                </div>
