                <div class="relative item" v-for="movie in upcoming.slice(0, 4)" :key="movie.id" :movie="movie" @click="selectMovie(movie)">              
                        <img class="mb-4 h-[200px] w-[450px] rounded-[20px] object-cover cursor-pointer border-[3px] border-transparent" :class="selectedMovie == movie ? 'border-white' : ''" :src="'https://image.tmdb.org/t/p/original/' + movie.backdrop_path" alt="">
