            <div class="mt-5 grid grid-cols-1 sm:grid-cols-3 md:grid-cols-3 lg:grid-cols-4 gap-8">
                <movie-item v-for="movie in otherMovies" :key="movie.id" :movie="movie" :genres="genres"/>
            </div>
        </div>
    </div>
</template>
<script>
import MovieItem from "@/components/items/MovieItem";

export default {
  components: {
    MovieItem,
  },
  data: function() {
    return {
      movies: [],
      genres: [],
      upcoming: [],
      selectedMovie: null,
      articles: [],
      firstPopularImage: null,
      firstTvShow: null,
      topMovie: null,
      news: null,
    };
  },
  computed: {
    firstMovie() {
      return this.movies.length > 0 ? this.movies[0] : null;
    },
    otherMovies() {
      return this.movies.slice(1);
    },
    headline() {
        return this.articles.length > 0 ? this.articles[0] : null;
    },
    showcaseImgSrc() {
        if (this.selectedMovie) {
        return "https://image.tmdb.org/t/p/original/" + this.selectedMovie.backdrop_path;
      } else if (this.upcoming.length > 0) {
        return "https://image.tmdb.org/t/p/original/" + this.upcoming[0].backdrop_path;
      } else {
        return null;
      }
    },
    formatDate() {
      const date = new Date(this.selectedMovie.release_date);
      
      const monthNames = [
        "Jan", "Feb", "Mar", "Apr", "May", "Jun",
        "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"
      ];
      
      const month = monthNames[date.getMonth()];
      const day = date.getDate();
      const year = date.getFullYear();
      
      return `${day} ${month}, ${year}`;
    }
  },
  async mounted() {
    this.fetchGenres();
    this.fetchPopular();
    this.fetchUpcoming();
    this.fetchFirstTvShow();
    this.fetchFirstMovie();
  },
  watch: {
    upcoming(newVal) {
      if (newVal.length > 0) {
        this.selectedMovie = newVal[0];
      }
    }
  },
  methods: {
    selectMovie(movie) {
      this.selectedMovie = movie; // set the clicked movie as the selected movie
    },
    async fetchPopular() {
        try {
            const response = await this.$tmdbApi.get("/movie/popular");
            this.movies = response.data.results;
        } catch (error) {
            console.log(error);
        }
    },
    async fetchGenres() {
      try {
        const response = await this.$tmdbApi.get("/genre/movie/list");
        this.genres = response.data.genres;
      } catch (error) {
        console.log(error);
      }
    },
    async fetchUpcoming() {
      try {
        const response = await this.$tmdbApi.get("/movie/upcoming");
        this.upcoming = response.data.results;
      } catch (error) {
        console.log(error);
      }
    },
    async fetchFirstTvShow() {
      try {
        const response = await this.$tmdbApi.get("/tv/top_rated");
        this.firstTvShow = response.data.results[0];
      } catch (error) {
        console.log(error);
      }
    },
    async fetchFirstMovie() {
      try {
        const response = await this.$tmdbApi.get("/movie/top_rated");
        this.topMovie = response.data.results[0];
      } catch (error) {
        console.log(error);
      }
    },
  }
};
</script>
