<template>
    <div>
        <router-link :to="`/movie/${movie.id}`">
            <img :src="posterPath" alt="" class="hover:opacity-75 rounded-[20px] transition-all	">
            <h3>{{movie.title}}</h3>
            <div>
                {{ movie.vote_average * 10 }} % | {{ movie.release_date }}<br/>
            </div>
            <span class="text-sm text-gray-500">
                <span :key="genre" v-for="(genre, index) in movie.genre_ids">
                    {{ genreTypeName(genre, index) }}
                </span>
            </span>
        </router-link>
    </div>
</template>
<script>
    export default {
        name: 'MovieItem',
        props: {
            movie: {
                required: true,
            },
            genres: {
                required: true,
            }
        },
        computed: {
            posterPath() {
                return "https://image.tmdb.org/t/p/w500/" + this.movie.poster_path
            }
        },
        methods: {
            genreTypeName(genreId, index) {
                for (const item of this.genres) {
                    if(item.id == genreId) {
                        if(this.movie.genre_ids.length - 1 == index) {
                            return item.name;
                        } else {
                            return item.name + ',';
                        }
                    }
                }
                // if(this.genres[genreId]) {
                //     if(this.genres.length - 1 == index) {
                //         return this.genres[genreId].name;
                //     } else {
                //         return this.genres[genreId].name + ',';
                //     }
                // }
            }
        },
    }
</script>
