<template>
    <div class="container mx-auto px-4 py-16">
        <div class="lg:flex sm:block align-start" >
            <img class="mx-auto mb-10 block" :src="'https://image.tmdb.org/t/p/w300'+ this.actor.profile_path" alt="">
            <div class="lg:ml-20">
                <h2 class="mb-5 text-4xl font-semibold">{{ this.actor.name }}</h2>
                <span class="text-gray-400 text-sm"></span>
                <p>{{ this.actor.biography }}</p>
                <ul class="flex items-center mt-4">
                    <li v-if="this.socialDetails.imdb_id" class="mr-6"><a :href="'https://www.imdb.com/name/' + this.socialDetails.imdb_id" target="_blank"><img src="@/assets/images/imdb.png" alt="imdb" class="w-[40px] h-[40px]"></a></li>
                    <li v-if="this.socialDetails.instagram_id" class="mr-6"><a :href="'https://www.instagram.com/' + this.socialDetails.instagram_id"><i class="ri-instagram-line text-[20px]"></i></a></li>
                    <li v-if="this.socialDetails.twitter_id" class="mr-6"><a :href="'https://twitter.com/' + this.socialDetails.twitter_id"><i class="ri-twitter-line text-[20px]"></i></a></li>
                </ul>
            </div>
        </div>
        <div class="container mx-auto">
            <h4 class="mt-12 mb-5 font-semibold text-[20px]">Known for</h4>
            <div class="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-5 gap-8">
                <div :key="movie.id" v-for="movie in this.knownFor">
                    <router-link 
                    :to="`/movie/${movie.id}`"
                    class="inline-block w-auto text-gray-400 text-sm leading-normal hover:text-white">
                        <img :src="movieImage(movie)" alt="" class="hover:opacity-75 transition ease-in-out duration-150">
                        {{ movie.name }}
                    </router-link>
                    
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            socialDetails: [],
            actor: {},
            knownFor: {},
            castMovies: {},
        };
    },
    mounted() {
        this.fetchActor(this.$route.params.id);
        this.fetchCredits(this.$route.params.id);
        this.fetchSocial(this.$route.params.id);
    },
    methods: {
        async fetchActor(actorId) {
            const response = await this.$tmdbApi.get(
                "https://api.themoviedb.org/3/person/" + actorId
            );
            this.actor = response.data;
        },
        async fetchCredits(actorId) {
            const response = await this.$tmdbApi.get(
                "https://api.themoviedb.org/3/person/" + actorId + "/combined_credits"
            );
            this.castMovies = response.data.cast;
            this.knownFor = response.data.cast.filter((x) => x.media_type == "movie").slice(1, 6);
        },
        async fetchSocial(actorId) {
            const response = await this.$tmdbApi.get(
                "https://api.themoviedb.org/3/person/" + actorId + "/external_ids"
            );
            this.socialDetails = response.data;
        },
        movieImage(movie) {
            const posterPath = movie.poster_path;
            if(!posterPath) {
                return "https://via.placeholder.com/185x278";
            }
            return "https://image.tmdb.org/t/p/w185/" + posterPath;
        }
    }
}
</script>
