import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Movie from '../components/movies/Movie.vue'
import Actors from '../components/actors/Actors.vue'
import ActorsDetails from '../components/actors/ActorDetails.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/movie/:id',
    name: 'movie',
    component: Movie
  },
  {
    path: '/actors',
    name: 'actors',
    component: Actors
  },
  {
    path: '/actor/:id',
    name: 'actorsDetails',
    component: ActorsDetails
  },
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
